.products {
  padding-top: 80px;
  padding-bottom: 40px;
  @media screen and(min-width:768px) {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  @media screen and(min-width:1280px) {
    padding-top: 60px;
  }
}
.products__list {
  @media screen and(min-width:768px) {
    display: flex;
    justify-content: center;
  }
}
// .products__item .is-open {
//   border-radius: 24px;
//   transform: rotateY(90deg);
//   animation: rotateY 3000ms 1 alternate forwards ease-in-out 1000ms;
// }
.products__item {
  // transform: rotateY(180deg);
  // animation: rotateY 3000ms infinite alternate backwards ease-in-out 1000ms;
  height: 637px;
  width: 280px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 140px;
  position: relative;

  @media screen and(min-width:768px) {
    padding-top: 138px;
    height: 611px;
    width: 224px;
    margin-left: 0;
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
  @media screen and(min-width:1280px) {
    padding-top: 261px;
    height: 796px;
    width: 395px;
    &:not(:last-child) {
      margin-right: 32px;
    }
  }
}
