.products__label {
  display: block;
  color: var(--title-font-color2);
  font-size: 18px;
  line-height: 1.17;
  margin-bottom: 15px;
  text-align: center;
  @media screen and(min-width:768px) {
    font-size: 26px;
    line-height: 1.15;
    margin-bottom: 18px;
  }
  @media screen and(min-width:1280px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 16px;
  }
}

.products__title {
  margin-bottom: 8px;
  @media screen and(min-width:768px) {
    margin-bottom: 8px;
  }
  @media screen and(min-width:1280px) {
    margin-bottom: 0;
  }
}
